<template>
    <div>
        <div class="loginNavFix">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">评价</p>
        </div>
        <div class="position">
            <div class="item-for">
                <div class="item-list ml-30" @click="clickThis(index)" v-for="(item,index) in list" :key="index">
                    <div class="item-title l f30 c333">六(2)班</div>
                    <img class="item-img r" src="../../../assets/my/more.png" alt="">
                    <div class="chooseClass" v-show="item.show">
                        <div class="item-list ml-30">
                            <div class="line l"></div>
                            <div class="item-title l f30">语文</div>
                        </div>
                        <div class="item-list ml-30">
                            <div class="line l"></div>
                            <div class="item-title l f30">数学</div>
                        </div>
                        <div class="item-list ml-30">
                            <div class="line l"></div>
                            <div class="item-title l f30">英语</div>
                        </div>
                    </div>
                </div>
                
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'HelloWorld',
    data () {
        return {
            msg: 'Welcome to Your Vue.js App',
            list : [{show:false},{show:false},{show:false},{show:false},{show:false}]
        }
    },
    methods : {
        returns(){
            this.$router.push({path:'/teacherstudent'});//返回上一层
        },
        clickThis(index){
            this.list[index].show = !this.list[index].show;
        }
        
    },
    created() {
    },  
    mounted() {
    },
}
</script>

<style scoped>
.position{position: absolute;top: 0.84rem;left: 0;right: 0;bottom: 0;overflow-y: auto;}
.item-for{width: 100%;overflow: hidden;}
.item-list{border-bottom: 1px solid #f2f2f2;min-height: 1rem;line-height: 1rem;}
.item-img{width: 0.3rem;height: 0.3rem;display: block;margin: 0.3rem 0.3rem 0 0;}
.chooseClass{transition: all 1s;clear: both;height: 100%;overflow: hidden;}
.chooseClass .item-list{color: #adadad;clear: both;}
.item-list .item-list:last-child{border: none;}
.line{width: 0.15rem;height: 0.02rem;background: #adadad;margin: 0.52rem 0.1rem 0 0 }
</style>



